<template>
  <div class="container mt-2">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <div class="card">
          <div class="card-header">
            <h3 class="text-center">로그인({{ $route.params.level }})</h3>
          </div>
          <div class="card-body">
            <form @submit.prevent="login">
              <div class="mb-3">
                <label for="username" class="form-label">아이디</label>
                <input type="text" v-model="username" class="form-control" id="username" required>
              </div>
              <div class="mb-3">
                <label for="password" class="form-label">비밀번호</label>
                <input type="password" v-model="password" class="form-control" id="password" required>
              </div>
              <button type="submit" class="btn btn-primary w-100">로그인</button>
              <button @click="goToJoin" class="btn btn-primary w-100">회원가입</button>
            </form>
            <div class="mt-2 text-center">
              <a href="#" @click.prevent="goToForgotUsername">아이디 찾기</a> |
              <a href="#" @click.prevent="goToForgotPassword">비밀번호 찾기</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  data() {
    return {
      username: '',
      password: ''
    };
  },
  mounted() {
    // 사용자 레벨 값 가져오기
    const userLevel = this.$route.params.level;
    
    // 가져온 사용자 레벨 값으로 로직 수행
    console.log('사용자 레벨:', userLevel);
  },
  methods: {

  

    login() {
      // 로그인 로직을 여기에 추가
      console.log('로그인 시도:', this.username, this.password);
    },
    goToJoin() {
      // 회원가입 페이지로 이동하는 로직을 추가
      const userLevel = this.$route.params.level;
      this.$router.push({ path: `/${userLevel}/join` });
      
      console.log('회원가입 페이지로 이동');
    },
    goToForgotUsername() {
      // 아이디 찾기 페이지로 이동하는 로직을 추가
      console.log('아이디 찾기 페이지로 이동');
    },
    goToForgotPassword() {
      // 비밀번호 찾기 페이지로 이동하는 로직을 추가
      console.log('비밀번호 찾기 페이지로 이동');
    }
  }
};
</script>

